@import url(https://cdn-uicons.flaticon.com/uicons-regular-rounded/css/uicons-regular-rounded.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 100%;
  height: 100%;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.nav-bar{
  display: -webkit-flex;
  display: flex;
}
.navbar{
  width: 100%;
  background-color: #98e698;
}

.dropdown-menu{
  text-align: center;
}
.footer{
  height: 3.6em;
  display: -webkit-flex;
  display: flex;
}
.navbar{
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.footer-text{
  font-family: 'Poppins', sans-serif;
  font-size: 0.9em;
  color: rgba(255,255,255,.85);
}
.landing{
  height: 100vh;
  background-color: #99e699;
}

.landing-nav{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 100%;
  height: 100%;
}

.landing-text{
  color: white;
  font-family: 'Poppins', sans-serif;
}

.landing-text h1{
  font-size: 5em;
}

#ht{
  color: #2eb82e !important; 
}

.about{
  font-family: 'Poppins', sans-serif;
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.about-intro{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.about .container{
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 3.2em;
}

.about-title{
  padding-top: 1.6em;
  font-weight: 700;
}
.about-image{
  width: 18em;
  border-radius: 9em;
}

.about-text{
  margin-top: 2.4em;
}

.about-text p{
  margin-top: 0;
  text-align: justify;
}

.about-middle{
  margin-top: 7.2em;
  background-color: #99e699;
  padding-bottom: 6.4em;
}

#main-middle-title{
  color: white;
  margin-bottom: 1.8em;
}

#middle-title{
  color: white;
}

.middle-col{
  margin-top: 1.2em;
}

.middle-col img{
  height: 4.8em;
}

.about-end{
  display: -webkit-flex;
  display: flex;
  margin-bottom: 5em;
}

.main-title{
  font-weight: 700;
}

.about-end .container{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  
}

.accordion{
  width: 50%;
}

.accordion-body{
  text-align: justify;
}

@media(max-width: 768px){

  .about-end .container{
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .accordion{
    margin-top: 1.6em;
    width: 100%;
  }

}



